	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>Indigenous Peoples are the first to inhabit Canada and have been the victims of colonialism;</p>\n</li>\n<li>\n<p>Indigenous Peoples have the inherent right to self-determination;</p>\n</li>\n<li>\n<p>Indigenous Peoples have guaranteed rights under section 35 of the constitution;</p>\n</li>\n<li>\n<p>Indigenous communities require sustainable, adequate and predictable funding;\n<strong>BE IT RESOLVED</strong> the Liberal Party of Canada support the end of proposal-based funding programs for Indigenous communities, in exchange for guaranteed and sustainable transfer payments.(Indigenous Peoples Commission)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/proposal-based-funding-to-sustainable-transfer-payments","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Proposal-based Funding to Sustainable Transfer Payments"}}},"pathContext":{}}

